<template><section><h2>ActionSheet </h2>
<template>
  <z-button :text="''" :type="'success'" @click.native="isShowActionSheet = true">
  </z-button>
<p><z-actionsheet :show="isShowActionSheet" :title="''" :lis="lis" :cancel-button="''" @click998="test" @after-close="isShowActionSheet = false">
</z-actionsheet>
</p></template><p></p>

<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-button</span>
<span class="hljs-attr">:text</span>=<span class="hljs-string">"''"</span>
<span class="hljs-attr">:type</span>=<span class="hljs-string">"'success'"</span>
