<h3>API</h3>
<template>
  <z-table :title="''" :ths="['','','','','']" :trs="[
    ['show','Boolean','','-',''],
    ['lis','Array','','-',''],
    ['title','String','','-',''],
    ['cancel-button','String','','','']
  ]">
  </z-table>
<p><z-table :title="'lis'" :ths="['','','','','']" :trs="[
    ['text','String','','-',''],
    ['id','Number','','-',''],
    ['href','String','','-','']
  ]">
</z-table>
</p></template><p></p>
<h4></h4>
<p>emit&quot;after-close&quot;</p>
<h4></h4>
<p>click(id),emit&quot;'click' + id&quot;,</p>
<p>998<code>@click998</code></p>
</section></template>

<script>
export default {
  data() {
    return {
      isShowActionSheet: false,
      lis: [
        {
          id: 998,
          text: ''
        },
        {
          text: '',
          href: 'http://blog.zhaoyuxiang.cn'
        },
        {
          text: ''
        }
      ]
    };
  },

  methods: {
    test() {
      alert('998item')
    }
  }
};
</script>


